//****************************************
// ChartDataPipeline
// Utility functions that load LabVIEW datatypes allowed for charts into history
// buffers
// National Instruments Copyright 2016
//****************************************

(function () {
    'use strict';

    // Static private reference aliases
    var NI_SUPPORT = NationalInstruments.HtmlVI.NISupport;

    NationalInstruments.HtmlVI.DataPipeline.ChartDataPipeline = function () {
        // Private variables
    };

    var graphDataPipeline = NationalInstruments.HtmlVI.DataPipeline.GraphDataPipeline;

    var proto = NationalInstruments.HtmlVI.DataPipeline.ChartDataPipeline.prototype;

    // Public prototype methods
    proto.loadValueIntoHistoryBuffer = function (value, historyBuffer) {
        switch (graphDataPipeline.prototype.inferDataType(value)) {
            case '1DNumericArray':
                historyBuffer.appendArray(value);
                break;
            case '2DNumericArray':
                var width = value.length;
                historyBuffer.setWidth(width);
                for (var i = 0; i < value[0].length; i++) {
                    var col = [];
                    for (var j = 0; j < width; j++) {
                        col[j] = value[j][i];
                    }
                    historyBuffer.push(col);
                }
                break;
            case 'empty':
                break;
            default:
                NI_SUPPORT.error('Unsupported value', this.value);
                break;
        }
    };

    proto.toFlot = function (chart, value, valueType) {
        // the chart is not using the element value as data, but the history buffer.
        // We return an array of empty data series with the length equal with the
        // width of the history buffer
        var res = [];
        var width = chart.historyBuffer.width;
        for (var i = 0; i < width; i++) {
            res[i] = [];
        }
        return res;
    }

}());
