//****************************************
// Cursor Custom Element
// DOM Registration: HTMLNICursor
// National Instruments Copyright 2017
//****************************************

/**
## ni-cursor

> Add cursors to a graph.

Sometimes you want to take a measurement off a graph you have made. Cursors allow
you to do that.

```html
<ni-cartesian-graph>
  <ni-cartesian-axis show grid-lines show-label label="Axis 1" axis-position='bottom'>
  </ni-cartesian-axis>
  <ni-cartesian-axis show grid-lines show-label label="Axis 2" axis-position='top'>
  </ni-cartesian-axis>
  <ni-cursor label="Cursor 1" show show-label snap-to-data cursor-color="#40d387"></ni-cursor>
</ni-cartesian-graph>
```
Multiple cursors can be added to a graph and configured.
*/

// Constructor Function: Empty (Not Invoked)
NationalInstruments.HtmlVI.Elements.Cursor = function () {
    'use strict';
};

// Static Public Variables
// None

(function (child, parent) {
    'use strict';
    // Static Private Reference Aliases
    var $ = NationalInstruments.Globals.jQuery;
    var NI_SUPPORT = NationalInstruments.HtmlVI.NISupport;

    NI_SUPPORT.inheritFromParent(child, parent);
    var proto = child.prototype;

    // Static Private Functions
    // None

    /** ### ni-cursor properties
    */
    proto.addAllProperties = function (targetPrototype) {
        parent.prototype.addAllProperties.call(this, targetPrototype);

        /** **show** (boolean) - if true the cursor will be visible. Default: false*/
        proto.addProperty(targetPrototype, {
            propertyName: 'show',
            defaultValue: false
        });

        /** **label** (string) - the label of the cursor. Default: '' */
        proto.addProperty(targetPrototype, {
            propertyName: 'label',
            defaultValue: ''
        });

        /** **show-label** (boolean) - if true the cursor label will be visible.
        Default: false */
        proto.addProperty(targetPrototype, {
            propertyName: 'showLabel',
            defaultValue: false
        });

        /**
        **target-shape** (string) - the shape of cursor. Default: 'ellipse'.
        Accepted values: 'ellipse', 'square', 'diamond', 'triangle', 'cross'
        */
        proto.addProperty(targetPrototype, {
            propertyName: 'targetShape',
            defaultValue: 'ellipse'
        });

        /**
        **color** (string) - the color of the cursor. Default: 'black'.
        Accepted values: a CSS color string
        */
        proto.addProperty(targetPrototype, {
            propertyName: 'cursorColor',
            defaultValue: '#000000'
        });

        /**
        **crosshair-style** (string) - the shape of crosshair. Default: 'both'.
        Accepted values: 'both', 'vertical', 'horizontal', 'none'
        */
        proto.addProperty(targetPrototype, {
            propertyName: 'crosshairStyle',
            defaultValue: 'both'
        });

        /** **show-value** (boolean) - if true the coordinates of the cursor will
        be visible. Default: false */
        proto.addProperty(targetPrototype, {
            propertyName: 'showValue',
            defaultValue: false
        });

        /** **snap-to-data** (boolean) - if true the cursor will snap to data.
        Default: false */
        proto.addProperty(targetPrototype, {
            propertyName: 'snapToData',
            defaultValue: false
        });

        /** **x** (number) - the x target coordinate of the cursor. This coordinate
        is relative to the visible area of the graph, in the interval 0 to 1.
        0 is on the left edge and 1 on the one to the right.
        Default: 0.5 */
        proto.addProperty(targetPrototype, {
            propertyName: 'x',
            defaultValue: 0.5
        });

        /** **y** (number) - the y target coordinate of the cursor. This coordinate
        is relative to the visible area of the graph, in the interval 0 to 1.
        0 is on the bottom edge and 1 on the top one.
        Default: 0.5 */
        proto.addProperty(targetPrototype, {
            propertyName: 'y',
            defaultValue: 0.5
        });

        /** **font-family** (string) - the font used for cursor label and values.
        Default: 'sans-serif' */
        proto.addProperty(targetPrototype, {
            propertyName: 'fontFamily',
            defaultValue: 'sans-serif'
        });

        /** **font-size** (string) - the font size used for cursor label and values.
        Default: '10px' */
        proto.addProperty(targetPrototype, {
            propertyName: 'fontSize',
            defaultValue: '10px'
        });

        /** **font-style** (string) - the font style used for cursor label and values.
        Default: '' */
        proto.addProperty(targetPrototype, {
            propertyName: 'fontStyle',
            defaultValue: ''
        });

        /** **font-size** (string) - the font weight used for cursor label and values.
        Default: '10px' */
        proto.addProperty(targetPrototype, {
            propertyName: 'fontWeight',
            defaultValue: ''
        });
    };

    proto.createdCallback = function () {
        parent.prototype.createdCallback.call(this);

        // Public Instance Properties
        this.cursor = null;

        // Private Instance Properties
        this._parentGraph = undefined;
    };

    proto.sendEventToParentGraph = function (name) {
        var eventConfig;

        if (this._parentGraph !== undefined) {
            eventConfig = {
                bubbles: true,
                cancelable: true,
                detail: {
                    element: this
                }
            };

            this._parentGraph.dispatchEvent(new CustomEvent(name, eventConfig));
        }
    };

    proto.attachedCallback = function () {
        var firstCall = parent.prototype.attachedCallback.call(this);

        if (this.parentElement instanceof NationalInstruments.HtmlVI.Elements.CartesianGraph ||
            this.parentElement instanceof NationalInstruments.HtmlVI.Elements.Chart) {
            this._parentGraph = this.parentElement;
            this.sendEventToParentGraph('ni-cursor-attached');
        } else {
            NI_SUPPORT.error('Cursor with the following id does not have a parentGraph', this.niControlId);
            this._parentGraph = undefined;
        }

        return firstCall;
    };

    /** TODO: clean up and document the events generated by cursors*/
    proto.propertyUpdated = function (propertyName) {
        parent.prototype.propertyUpdated.call(this, propertyName);

        this.sendEventToParentGraph('ni-cursor-changed');
    };

    proto.detachedCallback = function () {
        parent.prototype.detachedCallback.call(this);

        this.sendEventToParentGraph('ni-cursor-detached');
        this._parentGraph = undefined;
    };

    proto.setCursor = function (cursor) {
        this.cursor = cursor;
    };

    proto.updateCursorElement = function (data) {
        // TODO mraj this._x and this._y are underlying private properties and should not be assigned to directly
        this._x = this.cursor.position.relativeX;
        this._y = this.cursor.position.relativeY;
        $(this).trigger('cursorUpdated', data);
    };

    proto.getViewConfig = function () {
        var style = window.getComputedStyle(this);
        var crosshairStyle =  this.crosshairStyle;
        var mode;

        if (crosshairStyle === 'both') {
            mode = 'xy';
        } else if (crosshairStyle === 'horizontal') {
            mode = 'y';
        } else if (crosshairStyle === 'vertical') {
            mode = 'x';
        } else if (crosshairStyle === 'none') {
            mode = '';
        } else {
            mode = null;
        }

        return {
            show: this.show,
            showLabel: this.showLabel,
            showValuesRelativeToSeries: this.showValue ? 0 : undefined,
            name: this.label,
            color: this.cursorColor,
            snapToPlot: this.snapToData ? 0 : undefined,
            mode: mode,
            symbol: this.targetShape,
            fontSize: style.fontSize,
            fontFamily: style.fontFamily,
            fontStyle: style.fontStyle,
            fontWeight: style.fontWeight,
            position: {
                relativeX: this.x,
                relativeY: this.y
            }
        };
    };

    proto.setFont = function (fontSize, fontFamily, fontWeight, fontStyle, textDecoration) {
        parent.prototype.setFont.call(this, fontSize, fontFamily, fontWeight, fontStyle, textDecoration);
        this.sendEventToParentGraph('ni-cursor-changed');
    };

    proto.defineElementInfo(proto, 'ni-cursor', 'HTMLNICursor');
}(NationalInstruments.HtmlVI.Elements.Cursor, NationalInstruments.HtmlVI.Elements.Visual));
