/*jslint nomen: true, devel:true*/
/*global NationalInstruments*/
//****************************************
// Cartesian Axis Custom Element
// DOM Registration: HTMLNICartesianAxis
// National Instruments Copyright 2017
//****************************************

/**
## ni-cartesian-axis

> Add axes to a graph.

```html
<ni-cartesian-graph>
  <ni-cartesian-axis show grid-lines show-label label="Axis 1" axis-position='bottom'>
  </ni-cartesian-axis>
  <ni-cartesian-axis show grid-lines show-label label="Axis 2" axis-position='top'>
  </ni-cartesian-axis>
  <ni-cartesian-axis show grid-lines show-label label="Axis 3" axis-position='left'>
  </ni-cartesian-axis>
</ni-cartesian-graph>
```
Multiple vertical and horizontal axes can be added to a graph and configured.

Axes can be placed to the right, left and on top or bottom of a graph, can use a
logarithmic scale if desired and can show time. A relative time format is supported
along with the absolute time format.

The minimum and maximum value of an axis can be specified. Autoscaling according
to the data ranges dispayed is also possible.
*/

NationalInstruments.HtmlVI.Elements.CartesianAxis = function () {
    'use strict';
};

// Static Public Variables
// None

(function (child, parent) {
    'use strict';
    // Static Private Reference Aliases
    var NI_SUPPORT = NationalInstruments.HtmlVI.NISupport;

    NI_SUPPORT.inheritFromParent(child, parent);
    var proto = child.prototype;
    var NINumericFormatters = window.NINumericFormatters;

    // Static Private Variables
    // None

    // Static Private Functions
    // None

    var escapeHtmlString = function (string) {
        var entityMap = {
            '&': '&amp;',
            '<': '&lt;',
            '>': '&gt;',
            '"': '&quot;',
            "'": '&apos;',
            '/': '&#x2F;',
            '`': '&#x60;'
        };

        return String(string).replace(/[&<>"'`\/]/g, function fromEntityMap (s) {
            return entityMap[s];
        });
    };

    var isTimeAxis = function (format) {
        return (format.indexOf('LVTime') === 0) || (format.indexOf('LVRelativeTime') === 0);
    };

    var toFlotTimeFormat = function (format) {
        if (format.indexOf('LVTime') === 0) {
            return '%A';
        }

        if (format.indexOf('LVRelativeTime') === 0) {
            return '%r';
        }

        return null;
    };

    var siTickFormatter = function (num, axis, precision) {
        if(precision && precision > axis.tickDecimals){
            return NINumericFormatters.prototype.toSiNotation(num, precision);
        }
        return NINumericFormatters.prototype.toSiNotation(num, axis.tickDecimals);
    };

    var roundUsing = function (roundFunction, number, precisionDigits) {
        var precision = Math.pow(10, precisionDigits);
        var tempNumber = number * precision;
        tempNumber = roundFunction(tempNumber);

        return tempNumber / precision;
    };

    // Nx or Gx
    // where N is for Fixed Notation and G for Significant Digits Precision
    // x is a natural number representing the number of decimals/digits
    var getDecTickFormatter = function (options) {
        if (options.indexOf('G') === -1 && options.indexOf('N') === -1) {
            //use flot default tick formatter
            return null;
        }

        return function (num) {
            var len = parseInt(options.slice(1));
            var value = num;

            if (options.indexOf('G') === 0) {
                return NINumericFormatters.prototype.toDecPrecisionNotation(value, len);
            } else {
                value = roundUsing(Math.floor, num, len);
                return NINumericFormatters.prototype.toDecFixedNotation(value, len);
            }
        };
    };

    var chooseTickFormatter = function (format, showTickLabels) {
        if (format.indexOf('LVSI') === 0) {
            return siTickFormatter;
        }
        // 'LVRelativeSeconds:Nx' or 'LVRelativeSeconds:Gx'
        // Ex: 'LVRelativeSeconds:N4'
        // where N represents the Fixed Notation,
        // G represents the Significant Digits Precision and
        // x is a number representing the number of decimals/digits
        if (format.indexOf('LVRelativeSeconds') === 0) {
            var colonMarkIndex = 18,
                decFormatTypeSubstring = format.slice(colonMarkIndex);
            return getDecTickFormatter(decFormatTypeSubstring);
        }

        // use flot tick formatter
        return null;
    };

    var getDefaultAutoScale = function (axis, scaletype) {
        if(scaletype === "auto") {
            if(axis._parentGraph.nodeName === "NI-INTENSITY-GRAPH") {
                return "exact";
            } else {
                if(axis._parentGraph.nodeName === "NI-CHART") {
                    return (axis.axisPosition === "bottom" || axis.axisPosition === "top")
                        ? "sliding-window"
                        : "loose";
                }

                return (axis.axisPosition === "bottom" || axis.axisPosition === "top")
                    ? "exact"
                    : "loose";
            }
        }

        return scaletype;
    };

    /**### ni-cartesian-axis properties*/
    proto.addAllProperties = function (targetPrototype) {
        parent.prototype.addAllProperties.call(this, targetPrototype);

        /** **axis-ref** - an axis id used to bind plots to the axes. Default: ''*/
        proto.addProperty(targetPrototype, {
            propertyName: 'axisRef',
            defaultValue: '',
        });

        /** **show** (boolean) - if true the axis will be visible. Default: false*/
        proto.addProperty(targetPrototype, {
            propertyName: 'show',
            defaultValue: false
        });

        /** **label** (string) - the name of the axis. Default: '' */
        proto.addProperty(targetPrototype, {
            propertyName: 'label',
            defaultValue: ''
        });

        /**
        **axis-position** (string) - the position of the axis. Default: 'left'
         Accepted values: 'top', 'bottom', 'left' and 'right'
        */
        proto.addProperty(targetPrototype, {
            propertyName: 'axisPosition',
            defaultValue: 'left'
        });

        /** **show-label** (boolean) - if true the axis label will be visible.
        Default: false */
        proto.addProperty(targetPrototype, {
            propertyName: 'showLabel',
            defaultValue: false
        });

        /** **minimum** (number) - the minimum axis value. Default: 0*/
        proto.addProperty(targetPrototype, {
            propertyName: 'minimum',
            defaultValue: 0
        });

        /** **maximum** (number) - the maximum axis value. Default: 1*/
        proto.addProperty(targetPrototype, {
            propertyName: 'maximum',
            defaultValue: 1
        });

        /** **auto-scale** (string) - if 'exact' or 'loose' the axis will adapt to the data
        ranges of the plots assigned to that axis. if 'none', the axis will adapt to the
        minimum and maximum options. For 'growexact' and 'growloose' the axis will adapt only when
        the new data value is bigger than the old one. Default: 'auto'*/
        proto.addProperty(targetPrototype, {
            propertyName: 'autoScale',
            defaultValue: 'auto'
        });

        /** **log-scale** (boolean) - if true the axis will be a log axis. Default: false*/
        proto.addProperty(targetPrototype, {
            propertyName: 'logScale',
            defaultValue: false
        });

        /**
        **format** (string) - the format of the tick labels. Default: ''
        TODO: document the format
        */
        proto.addProperty(targetPrototype, {
            propertyName: 'format',
            defaultValue: ''
        });

        /** **show-tick-labels** (string) - which of the tick labels are visible.
        Possible values: 'none', 'endpoints', 'major' and 'all'. Default: 'all'.
        For 'endpoints' only the minimum and the maximum values will be visible
        at the edges of the axis. To make some intermediary values visible use
        'major', and 'all' to show all of them.*/
        proto.addProperty(targetPrototype, {
            propertyName: 'showTickLabels',
            defaultValue: 'all'
        });

        /** **grid-lines** (boolean) - if true the grid lines for the axis will
        be visible. Default: false*/
        proto.addProperty(targetPrototype, {
            propertyName: 'gridLines',
            defaultValue: false
        });

        /** **show-ticks** (boolean) - if true the tick lines for the axis will
        be visible. A tick line is a short line which connects the tick label with
        the axis. Default: false*/
        proto.addProperty(targetPrototype, {
            propertyName: 'showTicks',
            defaultValue: false
        });

        /** **show-minor-ticks** (boolean) - if true the minor tick lines for the axis
        will be visible. Minor tick lines are short lines (half of the tick line
        length) which subdivide the interval between tick lines in five intervals.
        Default: false*/
        proto.addProperty(targetPrototype, {
            propertyName: 'showMinorTicks',
            defaultValue: false
        });
    };

    proto.createdCallback = function () {
        parent.prototype.createdCallback.call(this);

        // Public Instance Properties
        // None

        // Private Instance Properties
        this.defaultMin = this._minimum;
        this.defaultMax = this._maximum;
        this._parentGraph = undefined;
    };

    proto.sendEventToParentGraph = function (name) {
        var eventConfig;

        if (this._parentGraph !== undefined) {
            eventConfig = {
                bubbles: true,
                cancelable: true,
                detail: {
                    element: this
                }
            };

            this._parentGraph.dispatchEvent(new CustomEvent(name, eventConfig));
        }
    };

    proto.attachedCallback = function () {
        var firstCall = parent.prototype.attachedCallback.call(this);

        if (this.parentElement instanceof NationalInstruments.HtmlVI.Elements.CartesianGraphBase) {
            this._parentGraph = this.parentElement;
            this.sendEventToParentGraph('ni-cartesian-axis-attached');
        } else {
            NI_SUPPORT.error('Axis with following id does not have a parentGraph', this.niControlId);
            this._parentGraph = undefined;
        }

        this.defaultMin = this._minimum;
        this.defaultMax = this._maximum;
        return firstCall;
    };

    proto.propertyUpdated = function (propertyName) {
        parent.prototype.propertyUpdated.call(this, propertyName);

        this.sendEventToParentGraph('ni-axis-changed');
    };

    proto.detachedCallback = function () {
        parent.prototype.detachedCallback.call(this);

        this.sendEventToParentGraph('ni-cartesian-axis-detached');
        this._parentGraph = undefined;
    };

    proto.setFont = function (fontSize, fontFamily, fontWeight, fontStyle, textDecoration) {
        parent.prototype.setFont.call(this, fontSize, fontFamily, fontWeight, fontStyle, textDecoration);

        this.sendEventToParentGraph('ni-axis-changed');
    };

    // here we transform the configuration from the one used by the custom element to the one understood by Flot
    proto.getViewConfig = function () {
        var currStyle = window.getComputedStyle(this),
            grow = (/grow/).test(this.autoScale),
            scaletype = grow ? this.autoScale.replace(/grow/, '') : this.autoScale;

        var flotConfig = {
            axisLabel: this.showLabel ? escapeHtmlString(this.label) : undefined,
            show: this.show,
            position: this.axisPosition,
            min: this.minimum,
            max: this.maximum,
            autoscale: getDefaultAutoScale(this, scaletype),
            growOnly: grow,
            mode: this.logScale ? 'log' : 'linear',
            element: this,
            format: isTimeAxis(this.format) ? 'time' : null,
            timeformat: toFlotTimeFormat(this.format),
            tickFormatter: chooseTickFormatter(this.format, this.showTickLabels),
            showTickLabels: this.showTickLabels,
            gridLines: this.gridLines,
            showTicks: this.showTicks,
            showMinorTicks: this.showMinorTicks,
            color: currStyle.color,
            navigateMin: this.navigateMin || 0,
            navigateMax: this.navigateMax || 0,
            font: {
                size: parseFloat(currStyle.fontSize),
                style: currStyle.fontStyle,
                weight: currStyle.fontWeight,
                family: currStyle.fontFamily,
                color: 'inherit'
            }
        };

        return flotConfig;
    };

    // get the flot axis coresponding to this element
    proto.getFlotAxis = function () {
        var graph = this._parentGraph.graph;
        var axes = graph.getAxes();
        var that = this;

        var key =  Object.keys(axes).filter(function (axis) {
            return axes[axis].options.element === that;
        })[0];

        return axes[key];
    };

    // set the axis min and max to the data min and max
    proto.scaleOnce = function () {
        var axis = this.getFlotAxis(),
            flot = this._parentGraph.graph,
            allSeries = flot.getData(),
            ranges = allSeries
                .filter(function(series) {
                    return series.xaxis === axis || series.yaxis === axis;
                })
                .map(function(series) {
                    return flot.computeRangeForDataSeries(series, true);
                }),
            defaultRange = {
                xmin: Number.POSITIVE_INFINITY,
                ymin: Number.POSITIVE_INFINITY,
                xmax: Number.NEGATIVE_INFINITY,
                ymax: Number.NEGATIVE_INFINITY
            },
            range = ranges.reduce(function(p, c) {
                    return {
                        xmin: Math.min(p.xmin, c.xmin),
                        ymin: Math.min(p.ymin, c.ymin),
                        xmax: Math.max(p.xmax, c.xmax),
                        ymax: Math.max(p.ymax, c.ymax)
                    };
                }, defaultRange);

        //this.autoScale = 'none';
        var min = axis.direction === 'x' ? range.xmin : range.ymin;
        var max = axis.direction === 'x' ? range.xmax : range.ymax;

        if(min !== Number.POSITIVE_INFINITY) this.minimum = min;
        else this.minimum = this.defaultMin;

        if(max !== Number.NEGATIVE_INFINITY) this.maximum = max;
        else this.maximum = this.defaultMax;

        this.navigateMin = axis.options.navigateMin = 0;
        this.navigateMax = axis.options.navigateMax = 0;
    };

    // called after an user interacts with the plot so the attributes of the element are in sync with the plot
    proto.syncWithFlot = function () {
        var axis = this.getFlotAxis();

        var autoscaleType = axis.options.autoscale;
        if(autoscaleType !== 'none') {
            this.autoScale = axis.options.growOnly ? ('grow' + autoscaleType) : autoscaleType;
        } else {
            this.autoScale = autoscaleType;
        }

        if (typeof axis.options.min === 'number') {
            this._minimum = axis.options.min;
            this._maximum = axis.options.max;
        }

        if(typeof axis.options.navigateMin === 'number') {
            this.navigateMin = axis.options.navigateMin;
        }

        if(typeof axis.options.navigateMax === 'number') {
            this.navigateMax = axis.options.navigateMax;
        }

        if (this._parentGraph !== undefined) {
            this._parentGraph.notifyScaleLegend('ni-axis-changed', this);
        }
    };

    proto.defineElementInfo(proto, 'ni-cartesian-axis', 'HTMLNICartesianAxis');

}(NationalInstruments.HtmlVI.Elements.CartesianAxis, NationalInstruments.HtmlVI.Elements.Visual));
