//****************************************
// GraphDataPipeline
// Utility functions that convert LabVIEW datatypes allowed for graphs to flot data series
// National Instruments Copyright 2016
//****************************************

/**
## Data types supported by the ni-cartesian-graph

Data can be passed to a graph either through writing to the element value
property or using API access (the setData() method on the element).

Here is a list of supported data types:
*/

(function () {
    'use strict';

    // Static private reference aliases
    // None

    NationalInstruments.HtmlVI.DataPipeline.GraphDataPipeline = function () {
        // Private variables
    };

    var DataTypesEnum = Object.freeze({
        niAnalogWaveform: 'niAnalogWaveform',
        niAnalogWaveformArray: 'niAnalogWaveformArray',
        XYClusterOfArrays: 'XYClusterOfArrays',
        XYArrayOfClusters: 'XYArrayOfClusters',
        XYArrayOfClusterOfArrays: 'XYArrayOfClusterOfArrays',
        XYArrayOfArrayOfClusters: 'XYArrayOfArrayOfClusters',
        oneDimensionNumericArray: '1DNumericArray',
        twoDimensionsNumericArray: '2DNumericArray',
        oneDimensionComplexArray: '1DComplexArray',
        twoDimensionsComplexArray: '2DComplexArray',
        empty: 'empty',
        unknown: 'unknown'
    });

    var graphDataPipeline = NationalInstruments.HtmlVI.DataPipeline.GraphDataPipeline;
    var ComplexNumber = window.NIComplex || NationalInstruments.HtmlVI.NIComplex;
    var AnalogWaveform = window.NIAnalogWaveform || NationalInstruments.HtmlVI.NIAnalogWaveform;

    var proto = graphDataPipeline.prototype;

    // Public prototype methods
    proto.toFlot = function (graph, value, valueType) {
        return convertToFlotFormat(graph, value, valueType);
    };

    // Private methods
    var isWaveform = function (value) {
        return (!Array.isArray(value)) && typeof value === 'object' && Array.isArray(value.Y);
    };

    var isWaveformArray = function (value) {
        return (Array.isArray(value)) && isWaveform(value[0]);
    };

    var isXYClusterOfArrays = function (value) {
        return (!Array.isArray(value)) && typeof value === 'object' &&
            Object.keys(value).length === 2 &&
            Object.keys(value).every(function (key) {
            return Array.isArray(value[key]);
        });
    };

    var isXYArrayOfClusters = function (value) {
        return (Array.isArray(value)) && (!Array.isArray(value[0])) &&
            typeof value[0] === 'object' && Object.keys(value[0]).length === 2 &&
            typeof value[0][Object.keys(value[0])[0]] === 'number';
    };

    var isXYArrayOfClusterOfArrays = function (value) {
        return Array.isArray(value) && isXYClusterOfArrays(value[0]);
    };

    var isXYArrayOfArrayOfClusters = function (value) {
        return Array.isArray(value) && isXYArrayOfClusters(value[0]);
    };

    var is1DComplexArray = function (value) {
        return Array.isArray(value) &&
        (typeof value[0] === 'string' || value[0] instanceof ComplexNumber);
    };

    var is2DComplexArray = function (value) {
        return Array.isArray(value) && Array.isArray(value[0]) &&
        (typeof value[0][0] === 'string' || value[0][0] instanceof ComplexNumber);
    };

    var inferDataType = function (data) {
        if (isWaveform(data)) {
            return DataTypesEnum.niAnalogWaveform;
        }

        if (isWaveformArray(data)) {
            return DataTypesEnum.niAnalogWaveformArray;
        }

        if (isXYClusterOfArrays(data)) {
            return DataTypesEnum.XYClusterOfArrays;
        }

        if (isXYArrayOfClusters(data)) {
            return DataTypesEnum.XYArrayOfClusters;
        }

        if (!(Array.isArray(data) || ArrayBuffer.isView(data))) {
            return DataTypesEnum.unknown;
        }

        if (data.length === 0) {
            return DataTypesEnum.empty;
        }

        if (typeof data[0] === 'number') {
            return DataTypesEnum.oneDimensionNumericArray;
        }

        if (isXYArrayOfClusterOfArrays(data)) {
            return DataTypesEnum.XYArrayOfClusterOfArrays;
        }

        if (isXYArrayOfArrayOfClusters(data)) {
            return DataTypesEnum.XYArrayOfArrayOfClusters;
        }

        if (is1DComplexArray(data)) {
            return DataTypesEnum.oneDimensionComplexArray;
        }

        if (is2DComplexArray(data)) {
            return DataTypesEnum.twoDimensionsComplexArray;
        }

        if (Array.isArray(data[0]) || ArrayBuffer.isView(data[0])) {
            return DataTypesEnum.twoDimensionsNumericArray;
        }

        return DataTypesEnum.unknown;
    };

    proto.inferDataType = inferDataType;

    var waveformToFlotFormat = function (waveform) {
        return {flatdata: true, data: waveform.Y, start: waveform.t0.toAbsoluteTime(), step: waveform.dt, decimate: NationalInstruments.HtmlVI.DataPipeline.decimateAW};
    };

    var xyClusterOfArraysToFlotFormat = function (xydata) {
        var result = [];
        var keys = Object.keys(xydata);
        var count = Math.min(xydata[keys[0]].length, xydata[keys[1]].length);

        for (var i = 0; i < count; i++) {
            result.push([xydata[keys[0]][i], xydata[keys[1]][i]]);
        }

        return result;
    };

    var xyArrayOfClustersToFlotFormat = function (xydata) {
        var result = [];

        var keys = Object.keys(xydata[0] || {});

        for (var i = 0; i < xydata.length; i++) {
            result.push([xydata[i][keys[0]], xydata[i][keys[1]]]);
        }

        return result;
    };

    var xyArrayOfClusterOfArraysToFlotFormat = function (xyDataArray) {
        return xyDataArray.map(function (xydata) {
            return xyClusterOfArraysToFlotFormat(xydata);
        });
    };

    var xyArrayOfArrayOfClustersToFlotFormat = function (xyDataArray) {
        return xyDataArray.map(function (xydata) {
            return xyArrayOfClustersToFlotFormat(xydata);
        });
    };

    var convertToFlotFormat = function (graph, data, valueType) {
        // jshint unused: vars
        var dataValuesArray = [];
        var dataValuesArray2D, waveform;

        var i, j;

        switch (inferDataType(data)) {

            /**
            * **A 1D array of numerics**
            ```js
            [a1, a2, a3]
            ```
            Will be drawn as a single plot. It's an efficient and fast way to
            pass data to the graph.

            A typed array view is also allowed.
            */
            case DataTypesEnum.oneDimensionNumericArray:
                return [{flatdata: true, data: data, start: 0, step: 1, decimate: NationalInstruments.HtmlVI.DataPipeline.decimate1D}];

            /**
            * **A 2D array of numerics**
            ```js
            [[a1, a2, a3], [b1, b2, b3]]
            ```
            Will be drawn as multiple plots. It's an efficient and fast way to
            pass data to the graph.

            An array of typed array views is also allowed.
            */
            case DataTypesEnum.twoDimensionsNumericArray:
                dataValuesArray2D = [];
                for (j = 0; j < data.length; j++) {
                    dataValuesArray = [];

                    dataValuesArray2D.push({flatdata: true, start: 0, step: 1, data: data[j], decimate: NationalInstruments.HtmlVI.DataPipeline.decimate1D});
                }

                return dataValuesArray2D;

            /**
            * **An analogWaveform**
            ```js
            var data = new NIAnalogWaveform({
                t0: new NITimestamp(),
                dt: 0.001, // 1 ms
                Y: [a1, a2, a3]
            });
            ```
            Will be drawn as a single plot. It's an
            efficient and fast way to pass data to the graph
            */
            case DataTypesEnum.niAnalogWaveform:
                if (data instanceof AnalogWaveform) {
                    waveform = data;
                } else {
                    waveform = new AnalogWaveform(data);
                }
                if (typeof waveform.channelName === 'string' && waveform.channelName.length > 0) {
                    graph.updatePlotName(0, waveform.channelName);
                }

                return [waveformToFlotFormat(waveform)];

            /**
            * **An array of analogWaveforms**
            ```js
            var aw0 = new NIAnalogWaveform({
                t0: new NITimestamp(),
                dt: 0.001, // 1 ms
                Y: [a1, a2, a3]
            });
            var aw1 = new NIAnalogWaveform({
                t0: new NITimestamp(),
                dt: 0.001, // 1 ms
                Y: [b1, b2, b3]
            });
            var data = [aw0, aw1];
            ```
            Will be drawn as multiple plots. It's an efficient and fast way to
            pass data to the graph.
            */
            case DataTypesEnum.niAnalogWaveformArray:
                dataValuesArray2D = [];
                for (j = 0; j < data.length; j++) {
                    if (data[j] instanceof AnalogWaveform) {
                        waveform = data[j];
                    } else {
                        waveform = new AnalogWaveform(data[j]);
                    }
                    if (typeof waveform.channelName === 'string' && waveform.channelName.length > 0) {
                        graph.updatePlotName(j, waveform.channelName);
                    }

                    dataValuesArray2D.push(waveformToFlotFormat(waveform));
                }

                return dataValuesArray2D;

            /**
            * **An object (cluster) of arrays**
            ```js
            var data = {a: [a1, a2, a3], b: [b1, b2, b3]};
            ```
             Will be drawn as a single XY plot,
            the first array will hold the X coordinates and the second the Y
            coordinates. This format is only recomended when you want to display
            XY data, it's not particulary efficient or fast.
            */
            case DataTypesEnum.XYClusterOfArrays:
                return [xyClusterOfArraysToFlotFormat(data)];

            /**
            * **An array of objects (clusters) of arrays**
            ```js
            var data = [
                {a: [a1, a2, a3], b: [b1, b2, b3]},
                {c: [c1, c2, c3], d: [d1, d2, d3]}
            ];
            ```
            Will be drawn as multiple XY plots, the first array in the object
            (cluster) will hold the X coordinates and the second the Y coordinates.
            This format is only recomended when you want to display XY data,
            it's not particulary efficient or fast.
            */
            case DataTypesEnum.XYArrayOfClusterOfArrays:
                return xyArrayOfClusterOfArraysToFlotFormat(data);

            /**
            * **An array of objects (clusters)**
            ```js
            var data = [
                {x: a1, y: b1},
                {x: a2, y: b2},
                {x: a3, y: b3},
            ];
            ```
            Will be drawn as a single XY plot, the first element in the cluster
            will hold the X coordinates and the second the Y coordinates. This
            format is only recomended when you want to display XY data, it's not
            particulary efficient or fast.
            */
            case DataTypesEnum.XYArrayOfClusters:
                return [xyArrayOfClustersToFlotFormat(data)];

            /**
            * **An array of array of objects (clusters)**
            ```js
            var data = [
                [{x: a1, y: b1}, {x: a2, y: b2}, {x: a3, y: b3}],
                [{x: c1, y: d1}, {x: c2, y: d2}, {x: c3, y: d3}]
            ];
            ```
            Will be drawn as multiple XY plots, the first element in the cluster
            will hold the X coordinates and the second the Y coordinates. This
            format is only recomended when you want to display XY data, it's not
            particulary efficient or fast.
            */
            case DataTypesEnum.XYArrayOfArrayOfClusters:
                return xyArrayOfArrayOfClustersToFlotFormat(data);

            /**
            * **An 1D array of Complex Numbers**
            ```js
            var a1 = new NIComplex(1, 2);
            var a2 = new NIComplex(2, 3);
            var a3 = new NIComplex(3, 4);
            var data = [a1, a2, a3]; // array of complex numbers
            var data2 = ['1+2i', '2+3i', '3+4i']; // also accepted as input
            ```
            Will be drawn as a single plot.
            */
            case DataTypesEnum.oneDimensionComplexArray:
                for (i = 0; i < data.length; i++) {
                    dataValuesArray.push(ComplexNumber.prototype.parseFromString(data[i]));
                }

                return [dataValuesArray];

            /**
            * **A 2D array of Complex Numbers**
            ```js
            var a1 = new NIComplex(1, 2);
            var a2 = new NIComplex(2, 3);
            var b1 = new NIComplex(3, 4);
            var b2 = new NIComplex(4, 5);
            var data = [[a1, a2], [b1, b2]]; // arrays of complex numbers
            var data2 = [['1+2i', '2+3i'], ['3+4i', '4+5i']]; // also accepted as input
            ```
            Will be drawn as multiple plots.
            */
            case DataTypesEnum.twoDimensionsComplexArray:
                dataValuesArray2D = [];
                for (j = 0; j < data.length; j++) {
                    dataValuesArray = [];
                    for (i = 0; i < data[j].length; i++) {
                        dataValuesArray.push(ComplexNumber.prototype.parseFromString(data[j][i]));
                    }

                    dataValuesArray2D.push(dataValuesArray);
                }

                return dataValuesArray2D;
            default:
        }

        return dataValuesArray2D || [dataValuesArray];
    };
}());
